<template>
	<template-auth 
    titulo="Recuperar senha"
    :progressLoader="progressLoader"
  >
    <form @submit.prevent="cadastrar">
      <v-text-field
        id="email"
        v-model="email"
        label="Informe seu e-mail"
        :rules="emailRules"
        v-on:keyup.enter="enterClick" 
      />
     
      <div class="text-center mt-4">
        <btn-confirm
          text="Enviar"
          :disabled="btnDisabled"
          @clicked="enviar"
        />
      </div>

      <div class="text-center mt-4">
				<btn-text
					text="Voltar"
          @clicked="goToBack()"
        />
      </div>
    </form>
  </template-auth>
</template>

<script>
// Components
import BtnConfirm from '@/components/globais/buttons/confirm/BtnConfirm'
import BtnText from '@/components/globais/buttons/text/BtnText'
import TemplateAuth from '@/pages/auth/template/Template'
// Mixins and MOdules
import Axios from "@/plugins/axios"
import NavigationMixin from '@/mixins/NavigationMixin'
import SnackbarMixin from '@/mixins/SnackbarMixin'
import ValidationMixin from '@/mixins/ValidationMixin'

export default {
  name: 'RecoveryPassword',
  components: { BtnConfirm, BtnText, TemplateAuth },
  mixins: [ NavigationMixin, SnackbarMixin, ValidationMixin ],
  data: () => ({
    progressLoader: false,
    email: ''  
  }),
  computed: {
    btnDisabled () {
      if (!this.isEmailValid(this.email)) return true
      return false
    }
  },
  mounted () {
    document.querySelector('#email').focus()
  },
  methods: {
    enterClick () {
      if (!this.btnDisabled) this.enviar()
      else this.showSnackbarErro('Informe seu e-mail!')
    },
    enviar () {
      this.progressLoader = true

      Axios.post('/auth/barber/recovery-password', { email: this.email })
      .then(response => {
        if (response.data.codigo === 200) {
          this.showSnackbar('A instrução para recuperar sua senha foi enviada para o e-mail informado!')
          this.goTo('barber-login')
        } else this.setErro(response.data.corpo)
      })
      .catch (() => { this.setErro('Não foi enviar o e-mail!') })
    },
    setErro (msg) {
      this.progressLoader = false
      this.showSnackbarErro(msg)
    }
  }
}
</script>